@import "../../global.scss";

.menu {
    width: 400px;
    height: 100vh;
    background-image: linear-gradient(120deg, $tetradicColor, $tertiaryColor, $secondaryColor, $mainColor);
    position: fixed;
    top: 0;
    right:-400px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;

    &.active {
        right: 0;
    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 35px;
        font-weight: 700;
        color: white;
        width: 60%;

        p{
            font-size: 20px;
        }

        .linked-in{
            height: 65px;
            width: 65px;

            img{
                height: 60px;
                width: 60px;
            }
        }

        .twitter{
            height: 65px;
            width: 65px;

            img{
                height: 60px;
                width: 60px;
            }
        }

        .github{
            height: 65px;
            width: 65px;

            img{
                height: 60px;
                width: 60px;
            }
        }

        li{
            margin-bottom: 40px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }

            &:hover {
                font-weight: 900;
            }
        }
    }
}